import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-pasos-consultor',
  templateUrl: './pasos.component.html',
  styleUrls: ['./pasos.component.css']
})
export class PasosConsultorComponent implements OnInit {

    @Input() public consultor:any = {};
    @Input() public tab = 0;
    public pasos:any = [];

    constructor() { }

    ngOnInit() {

        this.pasos = [
            {'id' : 1, 'titulo' : 'Consultor', 'rows' : 4, 'url': 'consultor'},
            {'id' : 2, 'titulo' : 'Especialidades', 'rows' : 4, 'url': 'especialidades'},
            {'id' : 3, 'titulo' : 'Historial', 'rows' : 4, 'url': 'historial'}
        ]

    }

}



