import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { AlertService } from '../../../../services/alert.service';
import { ApiService } from '../../../../services/api.service';
import { PasosConsultorComponent } from '../pasos.component';

@Component({
  selector: 'app-historial',
  templateUrl: './historial.component.html',
  styleUrls: ['./historial.component.css']
})
export class CHistorialComponent implements OnInit {

    public consultor: any = {};
    public historial: any = [];
    public loading = false;

    @ViewChild(PasosConsultorComponent) pasos: PasosConsultorComponent;

    constructor( 
        private apiService: ApiService, private alertService: AlertService,
        private route: ActivatedRoute, private router: Router
    ) { }

    ngOnInit() {
        this.pasos.tab = 2;
        
        this.route.params.subscribe(params => {
            
            if(isNaN(params['id'])){
                this.historial = {};
            }
            else{
               this.apiService.read('consultor/', params['id']).subscribe(consultor => {
                   this.consultor = consultor;
                   this.pasos.consultor = consultor;
                   this.apiService.read('consultor/historial/', consultor.id).subscribe(historial => {
                     this.historial = historial; 
                   });
               });
            }

        });

    }


}



